

























































import {
  reactive,
  defineComponent,
  toRefs,
  useRoute,
  Ref,
  ref,
  useContext,
} from "@nuxtjs/composition-api";
import { currentUserInfo } from "~/store";
import { useMessage } from "~/store/message";
import { UserMessagesType, UserPageType } from "~/store/types";
import _ from "lodash";
import { useUser } from "~/store/user";
import { VEmojiPicker } from "v-emoji-picker";

export default defineComponent({
  components: {
    VEmojiPicker,
  },
  setup() {
    const state: UserMessagesType = reactive({
      price: "",
      message: "",
      senderID: currentUserInfo._id,
      receiverID: "",
      messages: [],
      users: [],
      display: false,
      apperance: true,
    });
    const showList = () => {
      state.apperance = true;
    };

    const hideList = () => {
      state.apperance = false;
    };
    const { createMessage, getUserMessages } = useMessage();
    const { getUserInfo } = useUser();

    const changeDisplay = () => {
      state.display = !state.display;
    };

    const selectEmoji = (emoji: {
      aliases: string[];
      category: string;
      data: string;
    }) => {
      state.message = emoji.data;
      state.display = !state.display;
    };

    const route = useRoute();
    const routeID = route.value.params.slug;
    state.receiverID = routeID;
    const rezervationID = route.value.params.id;

    let filteredUsers: string[] = [];

    const user = ref({});
    if (currentUserInfo.provider) {
      const data = getUserInfo(currentUserInfo._id);
      data.then((dt: UserPageType) => {
        user.value = dt;
      });
    }

    const offers: Ref<
      {
        _id: String;
        price: String;
        clientID: String;
        bidderID: String;
        serviceID: String;
      }[]
    > = ref([]);
    const { getOffer } = useMessage();
    const setOffer = async () => {
      offers.value = await getOffer(currentUserInfo._id);
    };

    setOffer();
    if (state.receiverID) {
      const data = getUserInfo(state.receiverID);
      data.then((dt: UserPageType) => {
        user.value = dt;
      });
    }

    const getMessages = async (): Promise<void> => {
      try {
        const messages: any = await getUserMessages(state.senderID);
        await messages.forEach(
          (msg: { receiverID: string; senderID: string }) => {
            filteredUsers.push(msg.receiverID);
            filteredUsers.push(msg.senderID);
          }
        );

        const us = _.uniq(filteredUsers);
        state.users = _.filter(us, (user: string) => user !== state.senderID);

        state.messages = _.filter(
          messages,
          (obj: { receiverID: string; senderID: string }) =>
            obj.receiverID == state.receiverID ||
            obj.senderID == state.receiverID
        );
      } catch (error) {
        console.error(error);
      }
    };

    const emptyForm = (): void => {
      state.price = "";
      state.message = "";
    };

    getMessages();

    const context = useContext();
    const io = context.app.$nuxtSocket({
      name: "main",
    });

    const socket = io.connect();
    socket.on("receive-message", async (message: any) => {
      state.messages.push(message);
    });
    const submit = async (): Promise<void> => {
      const price = state.price;
      const message = state.message;
      const senderID = state.senderID;
      const receiverID = state.receiverID;
      const asked_service_id = rezervationID;
      socket.emit("send-message", {
        price,
        message,
        senderID,
        receiverID,
        rezervationID,
      });
      await createMessage(
        asked_service_id,
        price,
        message,
        senderID,
        receiverID
      );
      emptyForm();
      await getMessages();
    };

    return {
      ...toRefs(state),
      submit,
      changeDisplay,
      selectEmoji,
      showList,
      hideList,
      user,
      offers,
    };
  },
});
